<template>
    <div id="articles">
      <v-container
        fluid
        grid-list-sm
      >
        <v-layout row wrap v-for="(item, index) in articles" :key="index">
          <v-flex xs12> 
            <v-card 
              flat
              class="custom-ripple mb-2"
              :ripple="{ class: 'primary--text' }"
              color="white"
              v-on:mouseover="addShadow($event)"
              v-on:mouseout="removeShadow($event)"
              >
                <a><v-card-text class="word" @click="handleClick(item.number)">{{ item.title }}</v-card-text></a>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </div>
</template>
<script>
import { getArticlesByRepoName } from "@/api";

export default {
  data() {
    return {
      articles: []
    };
  },
  created() {
    getArticlesByRepoName("guanpengchn.github.io", 1, 10).then(articles => {
      this.articles = articles;
    });
  },
  methods: {
    handleClick: function(number) {
      this.$router.push("/article/" + number);
    },
    addShadow: function($event) {
      $event.currentTarget.className += " elevation-3"
    },
    removeShadow: function($event) {
      $event.currentTarget.className = $event.currentTarget.className.replace(/ elevation-3/g," ")
    }
  }
};
</script>
<style scoped>
.word {
  font-size:14px;
  padding: 12px 30px;
  font-weight: normal;
}
a {
  color:black;
}
a:hover{
  color:#2196F3;
}
</style>


